@import "../../variables.scss";

.section {
    display: flex;
    flex-direction: column;
    gap: calc(#{$offset-base-size} * 4);
    /* height: calc(912px - 88px - 100px - 56px); */
}

.tabsList {
  overflow-y: auto;
  height: calc(912px - 188px - 56px);
}

.navList {
  display: flex;
}

.ingredientsList {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: calc(#{$offset-base-size} * 6);
}

.ingredientsItem {
  text-align: center;
  position: relative;
  cursor: move;

  & .isDrag,
  &:has(> svg) {
    opacity: 40%;
  }

  & > img {
    pointer-events: none;
  }
}


.ingredientsItem > svg {
  position: absolute;
  margin: calc(#{$offset-base-size} * 2);
}

.counter {
  position: absolute;
  top: 0;
  right: 0;
}

.ingredientsPrice {
  display: flex;
  justify-content: center;
}
