@import '../../variables.scss';

.body {
  min-width: 640px;
  display: flex;
  flex-direction: column;
  background: $color-modal-bg;
  border-radius: calc(#{$offset-base-size} * 10);
  box-shadow: 0px 0px 16px 8px rgba(51, 51, 255, 0.25),
    0px 0px 8px 8px rgba(51, 51, 255, 0.25);
  filter: drop-shadow(0px 4px 32px rgba(51, 51, 255, 0.5));
}

.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 40px;
}

.header svg {
  cursor: pointer;
}

.header svg:hover {
  animation: rotate-once 1s ease-in-out;
}

@keyframes rotate-once {
  50% {
    transform: rotate(90deg);
  }
  100% {
    transform: rotate(90deg);
  }
}

.title {
  margin-right: auto;
}
